import { CommonModule } from '@angular/common';
import {
  Component,
  effect,
  EventEmitter,
  input,
  Input,
  Output,
  signal,
} from '@angular/core';
import { IPageChangeEvent } from '../../interfaces';

@Component({
  selector: 'custom-pagination',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './custom-pagination.component.html',
  styleUrls: ['./custom-pagination.component.css'],
})
export class CustomPaginationComponent {
  @Input() maxVisiblePages: number = 10;

  @Input({ required: true }) page: number = 1;
  @Input() pageSize: number = 10;

  private _totalCount = 0;

  @Input({ required: true })
  set totalCount(value: number) {
    this._totalCount = value;
    this.calculateTotalPages();
  }

  get totalCount(): number {
    return this._totalCount;
  }
  @Output()
  pageChange = new EventEmitter<IPageChangeEvent>();
  @Input() baseValue: number = 10;
  pageSizeOptions = signal<number[]>([]);

  totalPages = signal<number[]>([]);
  hideTotalCount = input<boolean>(false);
  @Input() showPageSize: boolean = true;

  constructor() {
    // effect(()=>{
    //   if(this.totalPages())
    //     this.calculateTotalPages();
    // })
  }

  ngOnInit(): void {
    this.calculateTotalPages();
  }

  calculateTotalPages() {
    this.generatePageSizeOptions();
    const pageCount = Math.ceil(this.totalCount / this.pageSize);

    const pages: number[] = [];

    if (pageCount <= this.maxVisiblePages) {
      for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
      }
    } else {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(this.page - half, 1);
      let end = start + this.maxVisiblePages - 1;

      if (end > pageCount) {
        end = pageCount;
        start = Math.max(end - this.maxVisiblePages + 1, 1);
      }

      for (let i = start; i <= end; i++) {
        pages.push(i);
      }
    }

    this.totalPages.set(pages);
  }

  generatePageSizeOptions() {
    this.pageSizeOptions.set([]);
    for (let i = 0; i < 4; i++) {
      this.pageSizeOptions().push(this.baseValue * Math.pow(2, i));
    }
  }

  prevPage() {
    if (this.page > 1) {
      this.page--;
      this.calculateTotalPages(); // 🟢 recalculate visible range
      this.pageChange.emit({ page: this.page, pageSize: this.pageSize });
    }
  }

  nextPage() {
    const pageCount = Math.ceil(this.totalCount / this.pageSize);
    if (this.page < pageCount) {
      this.page++;
      this.calculateTotalPages(); // 🟢 update
      this.pageChange.emit({ page: this.page, pageSize: this.pageSize });
    }
  }
  changePage(index: number) {
    this.page = index;
    this.calculateTotalPages(); // 🟢 update visible pagination window
    this.pageChange.emit({ page: this.page, pageSize: this.pageSize });
  }

  firstPage() {
    if (this.page > 1) {
      this.page = 1;
      this.calculateTotalPages();
      this.pageChange.emit({ page: this.page, pageSize: this.pageSize });
    }
  }

  lastPage() {
    const pageCount = Math.ceil(this.totalCount / this.pageSize);
    if (this.page < pageCount) {
      this.page = pageCount;
      this.calculateTotalPages();
      this.pageChange.emit({ page: this.page, pageSize: this.pageSize });
    }
  }

  onPageSizeChange(event: Event) {
    const selectElement = event.target as HTMLSelectElement;
    this.pageChange.emit({
      page: this.page,
      pageSize: +selectElement.value, // The + converts string to number
    });
  }
}
