import { Component, Input } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'custom-actions-dropdown',
  imports: [],
  templateUrl: './custom-actions-dropdown.component.html',
  styleUrl: './custom-actions-dropdown.component.css'
})
export class CustomActionsDropdownComponent {
  @Input() actions: { label: string; icon?: string; callback: (ctx: any) => void }[] = [];
  @Input() context: any;
  @Input() horizontalDots: boolean = false;

  isOpen = false;
constructor(private sanitizer: DomSanitizer) {}

  sanitizeSvg(svg: string): SafeHtml {
    return this.sanitizer.bypassSecurityTrustHtml(svg);
  }
  openDropdown(): void {
    this.isOpen = true;
  }

  closeDropdown(): void {
    this.isOpen = false;
  }

  onClickAction(action: { callback: (ctx: any) => void } , event: MouseEvent): void {
      event.stopPropagation();
    action.callback(this.context);
    this.closeDropdown();
  }
}
