import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormsModule } from '@angular/forms';
import { debounceTime, distinctUntilChanged, Subject, Subscription } from 'rxjs';

@Component({
  selector: 'custom-reactive-search-input',
  imports: [NgClass , FormsModule],
  templateUrl: './custom-reactive-search-input.component.html',
  styleUrl: './custom-reactive-search-input.component.css'
})
export class CustomReactiveSearchInputComponent {
  @Input() model: string = '';
  @Output() modelChange = new EventEmitter<string>();

  @Output() search = new EventEmitter<string>();

  @Input({required:true}) containerClass = '';
  @Input({required:true}) inputClass = '';
  @Input({required:true}) inputPlaceholder = '';

  private inputSubject = new Subject<string>();
  constructor(){
     this.inputSubject
    .pipe(
      debounceTime(1000),
      distinctUntilChanged(),
      takeUntilDestroyed()
    )
    .subscribe((val) => this.search.emit(val.trim()));
  }

  onInputChange(value: string) {
    this.modelChange.emit(value); // for ngModel sync
    this.inputSubject.next(value); // for debounce emit
  }
}
